import React, {useCallback, useState} from 'react';
import { Link, useParams, useNavigate } from 'react-router-dom'
import Vimeo from '@u-wave/react-vimeo'
import courses from '../courses'
import StepProgressBar from "react-step-progress"
import "react-step-progress/dist/index.css";
import {Piano, KeyboardShortcuts, MidiNumbers} from 'react-piano';
import 'react-piano/dist/styles.css';
import SoundfontProvider from './SoundfontProvider';

function Lesson() {
  const { courseId, lessonId } = useParams()
  const course = courses.find(course => course.id === parseInt(courseId))
  const lesson = course.lessons.find(lesson => lesson.id === parseInt(lessonId))
  const nextLessonId = () => {
    const currentIndex = course.lessons.indexOf(lesson)
    const nextIndex = (currentIndex + 1) % course.lessons.length
    return course.lessons[nextIndex].id
  }
  const content = <div style={{paddingTop: '30px'}} className="Content"><Vimeo video={lesson.vimeoId} responsive /></div>
  var s = [];
  for (var i = 0; i < course.lessons.length; i++) {
      var c = <div style={{paddingTop: '30px'}} className="Content"><Vimeo video={course.lessons[i].vimeoId} responsive /><p>{course.lessons[i].content}</p></div>
      s.push({label: course.lessons[i].title, name: course.lessons[i].title, content: c})
  }
  const navigate = useNavigate()
  const[show,setShow] = useState(true);

  const audioContext = new (window.AudioContext || window.webkitAudioContext)();
  const soundfontHostname = 'https://d1pzp51pvbm36p.cloudfront.net';

  const noteRange = {
    first: MidiNumbers.fromNote('c3'),
    last: MidiNumbers.fromNote('f4'),
  };
  const keyboardShortcuts = KeyboardShortcuts.create({
    firstNote: noteRange.first,
    lastNote: noteRange.last,
    keyboardConfig: KeyboardShortcuts.HOME_ROW,
  });
  return (
    <div className="Lesson page">
      <header>
        <div class="nav">
          <p><Link to={'/courses/' + course.id}>Back to {course.title}</Link></p>
          <p><button onClick={()=>setShow(!show)}>Show/hide piano</button></p>
        </div>
        {show?null:<SoundfontProvider
          instrumentName="acoustic_grand_piano"
          audioContext={audioContext}
          hostname={soundfontHostname}
          render={({ isLoading, playNote, stopNote }) => (
            <Piano
              noteRange={noteRange}
              width={800}
              playNote={playNote}
              stopNote={stopNote}
              disabled={isLoading}
              keyboardShortcuts={keyboardShortcuts}
            />
          )}
        />}
        <h1>{lesson.title}</h1>
      </header>
      <StepProgressBar
        startingStep={lesson.id-1}
        submitBtnName="Finish"
        steps={s}
        onSubmit={
          useCallback(() => navigate('/courses/' + course.id, {replace: true}), localStorage.setItem(courseId + " " + lessonId, "1"), [navigate])
        }
      />
    </div>
  )
}

export default Lesson